  }
}
img {
  max-width: 100%;
  height: auto;
  display: block;
  object-fit: cover;
}

input[type=submit],
button {
  cursor: pointer;
}

hr {
  display: block;
  height: 0.1em;
  border: 0;
  border-top: 0.1em solid #ccc;
  margin: 1em 0;
  padding: 0;
}

textarea {
  resize: vertical;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

a {
  text-decoration: none;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
}

html,
body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans Regular", "Arial", "Tahoma", sans-serif;
  font-weight: 400;
  font-style: normal;
  text-rendering: optimizelegibility;
  overflow-x: hidden;
}

header,
section,
footer,
main {
  width: 100%;
}

.wrapper {
  width: 100%;
  min-height: 100vh;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container {
  width: 100%;
  max-width: 144rem;
  padding: 0 1.5rem;
  margin: 0 auto;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.no-scroll {
  overflow: hidden;
}
@media (min-width: 1024px) {
  .no-scroll {
    padding-right: 1.7rem;
  }
}

h2 {
  font-family: "Roboto Bold";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  font-size: 3.5rem;
  line-height: 130%;
}
