  h2 {
    font-size: 4.5rem;
  }
}
@media (min-width: 1440px) {
  h2 {
    font-size: 6rem;
  }
}

.yellow {
  background-image: linear-gradient(0deg, #F96E09 0%, #ECD823 100%);
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
}

.btn {
  font-family: "Open Sans Regular";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  font-size: 1.4rem;
  line-height: 130%;
  color: var(--black_1);
  outline: none;
  border: none;
  border-radius: 0.5rem;
  background: linear-gradient(0deg, #F96E09 0%, #F1AF19 38.02%, #EFBC1C 45.31%, #ECD823 61.46%);
  will-change: box-shadow;
  transition: box-shadow 0.3 ease-in-out;
}
